.choice {
    display: flex;
    align-items: center;
    justify-content: center;

    &>*:not(:first-child):not(:last-child) {
        margin-right: 10px;
    }

    &>span:not(:last-child) {
        opacity: 0.6;
    }

    &>input[type=checkbox] {
        height: 0;
        width: 0;
        visibility: hidden;
    }

    &>input:checked + label {
        background: hsl(174, 58%, 74%);
    }

    &>input:checked + label:after {
        left: calc(100% - 2px);
        transform: translateX(-100%);
    }

    &>label {
        cursor: pointer;
        text-indent: -9999px;
        width: 40px;
        height: 20px;
        background: grey;
        display: block;
        border-radius: 10px;
        position: relative;
    }

    &>label::after {
        content: '';
        position: absolute;
        top: 2px;
        left: 2px;
        width: 16px;
        height: 16px;
        background: #fff;
        border-radius: 90px;
        transition: 0.3s;
    }

    &>label:active:after {
        width: 13px;
    }

}

.discount {
    padding: 3px 8px;
    background-color: hsl(14, 92%, 95%);
    color: hsl(15, 100%, 70%);
    border-radius: 15px;
}

.discount-mobile {
    display: none;
}

@media (max-width: 650px) {
    .choice {
        font-size: 12px;
    }

    .discount {
        font-size: 12px;
    }
}

@media (max-width: 375px) {
    .choice {
        font-size: 10px;

        &>span:not(:last-child) {
            margin-right: 0;
            width: 40px;
        }
    }

    .discount {
        display: none;
    }

    .discount-mobile {
        display: block;
        font-size: 10px;
        padding: 3px 8px;
        background-color: hsl(14, 92%, 95%);
        color: hsl(15, 100%, 70%);
        border-radius: 15px;
    }
}
