.confirmation {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 40px;
    

    &>button {
        cursor: pointer;
        border: none;
        background-color: hsl(227, 35%, 25%);
        color: hsl(226, 100%, 87%);
        padding: 10px 50px;
        border-radius: 100px;

        &:hover {
            color: white;
        }
    }
}

.advantage {
    *:not(:last-child) {
        margin-right: 15px;
    }

    &>span {
        opacity: 0.6;
    }
}

@media (max-width: 650px) {
    .confirmation {
        flex-direction: column;
        gap: 20px;
        padding: 20px;
    }

}

@media (max-width: 375px) {

    .confirmation {
        &>button {
            font-size: 10px;
        }
    }

    .advantage {
        &>span {
            font-size: 10px;
        }
    }
}