.header {
    height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-image: url(../../assets/pattern-circles.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    letter-spacing: 1px;
    margin-bottom: 50px;
}

.title {
    font-size: 28px;
    margin: 20px 0 10px 0;
}

.info {
    font-size: 15px;
    margin: 0;
    opacity: 0.6;
}

@media (max-width: 650px) {
    .title {
        font-size: 20px;
    }

    .info {
        width: 270px;
        font-size: 13px;
    }
}

@media (max-width: 375px) {
    .title {
        font-size: 18px;
    }

    .info {
        width: 200px;
        font-size: 12px;
    }
}