.main {
    background-color: white;
    width: max-content;
    width: 70%;
    max-width: 550px;
    font-size: 14px;
    border-radius: 10px;
    box-shadow: 5px 5px 20px 0 rgba(107, 104, 104, 0.3);
}

.main-handle {
    padding: 40px;
    border-bottom: 1px solid rgba(174, 172, 172, 0.33);
}

@media (max-width: 650px) {
    .main-handle {
        padding: 20px;
    }
}