
.slider {
    margin: 40px 0;

    &>input {
        -webkit-appearance: none;
        appearance: none;
        width: 100%;
        outline: none;
        border-radius: 15px;
        height: 40px;
        height: 10px;
        cursor: pointer;

        &::-webkit-slider-thumb:hover {
            box-shadow: 0 0 0 10px rgba(16, 213, 194, 0.1);
            background-color: hsl(174, 86%, 45%);
        }

        &:active::-webkit-slider-thumb {
            box-shadow: 0 0 0 13px rgba(16, 213, 194, 0.2);
            background-color: hsl(174, 86%, 45%);
        }

        &:focus::-webkit-slider-thumb {
            box-shadow: 0 0 0 13px rgba(16, 213, 194, 0.2);
            background-color: hsl(174, 86%, 45%);
        }

        &::-webkit-slider-thumb {
            -webkit-appearance: none;
            appearance: none;
            background-color: hsl(174, 58%, 74%);
            background-image: url(../../assets/icon-slider.svg);
            background-position: center;
            background-repeat: no-repeat;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            border: none;
            transition: .2s ease-in-out;
        }
    } ;
}

@media (max-width: 650px) {
    .slider {
        &>input {
            &::-webkit-slider-thumb {
                width: 35px;
                height: 35px;
            }

            &::-webkit-slider-thumb:hover {
                box-shadow: 0 0 0 6px rgba(16, 213, 194, 0.1);
            }

            &:active::-webkit-slider-thumb {
                box-shadow: 0 0 0 8px rgba(16, 213, 194, 0.2);
            }

            &:focus::-webkit-slider-thumb {
                box-shadow: 0 0 0 8px rgba(16, 213, 194, 0.2);
            }
        }
    }
}

@media (max-width: 375px) {
    .slider {
        margin: 30px 0;

        &>input {
            height: 8px;
        }
    }
}