.price {
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    &>span {
        letter-spacing: 1px;
        opacity: 0.6;
    }

    &>div {
        display: flex;
        align-items: center;
        text-align: center;

        &>span {
            padding-left: 5px;
            opacity: 0.6;
        }
    }

}

.number {
    font-size: 30px;
}

@media (max-width: 650px) {
    .price {
        flex-direction: column;
        gap: 10px;

        &>span {
            font-size: 13px;
        }
    }
}

@media (max-width: 375px) {
    .price {
        &>div {
            &>strong {
                font-size: 26px;
            }
        }
    }
}